  .window {
      position: fixed;
      background-color: rgba(19, 19, 19, 0.25);
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 99999999;
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:target {
          visibility: visible;
          opacity: 1;
          pointer-events: auto;
      }
      &>div {
          max-width: 400px;
          max-height: 600px;
          overflow-y: auto;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          padding: 2em;
          background: #ffffff;
          box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
      }
      header {
          font-weight: bold;
      }
      h1 {
          font-size: 150%;
          margin: 0 0 15px;
      }
      .modal-close {
          color: #aaa;
          line-height: 50px;
          font-size: 80%;
          position: absolute;
          right: 0;
          text-align: center;
          top: 0;
          width: 70px;
          text-decoration: none;
          &:hover {
              color: black;
          }
      }
  }